<template>

<div class="container mt-5">
  <vue-particles
    color="#7B1FA2"
    :particleOpacity="0.9"
    :particlesNumber="120"
    shapeType="polygon"
    :particleSize="6"
    linesColor="#00acd6"
    :linesWidth="1"
    :lineLinked="true"
    :lineOpacity="0.4"
    :linesDistance="100"
    :moveSpeed="6"
    :hoverEffect="true"
    hoverMode="grab"
    :clickEffect="true"
    clickMode="repulse"
>
</vue-particles>
  <div class="row p-3">
    <h4> KANBAN BOARD  :</h4><h5 class="ml-3">Muhammad Izzat Syaza Bin Suhaimi <font-awesome-icon icon="coffee" /></h5>
  </div>
  <div class="row">
    <div class="col form-inline">
      <b-form-input v-model="newTask" placeholder="Enter Task" @keyup.enter="add"></b-form-input>
      <b-button class ="mr-5" variant="primary" @click="add">Add</b-button>
      <b-form-input v-model="newColumn" placeholder="Create New Column" @keyup.enter="createColumn"></b-form-input>
      <b-button  variant="info" @click="createColumn">Create</b-button>
      
    </div>
  </div>
  <div class="row mt-4">
    <div v-for="(column, i) in taskColumn" :key="column">
    <div class="col-md-12">
      <div class="p-4 alert alert-success">
        <div :class="{editing: column == editedColumn}">
        <div class="row">
          <div class="viewcolumn">
          <label  @dblclick="editColumn(column)">  <h4>{{column.title}} </h4></label>
      
        <button class="trashb ml-5 mb-3" small @click="deleteColumn(i)"><font-awesome-icon icon="trash" /></button>
        </div></div>
        <input class="editColumn" type="text"
							v-model="column.title"
							@blur="doneEditColumn(column, i)"
							@keyup.enter="doneEditColumn(column, i)" />
        </div>
        <draggable class="list-group " :list="column.tasks" group="tasks">
          <div class="list-group-item p-2" v-for="(task, index) in column.tasks" :key="task.id">
            <div :class="{editing: task == editedTask}">
            <div class="view">
            <label  @dblclick="editTask(task)">{{task.name}}</label>
            <button class="destroy pl-3" @click="remove(index, i)"></button>
            
            </div>
            <input class="edit" type="text"
							v-model="task.name"
							@blur="doneEditTask(task, index, i)"
							@keyup.enter="doneEditTask(task, index, i)" />
          </div>
          </div>
        </draggable>
      </div>
    </div>
    </div>

  </div>
</div>

</template>
<script>
import draggable from "vuedraggable";

const STORAGE_KEY = 'task-storage';

export default {
  name: 'App',
  components: {
    draggable
  },
  data() {
    return {
      newTask: "",
      newColumn: "",

      taskColumn: [
      
      ], 
      editedTask: null,
      editedColumn: null
    };
  },
  created () {
    this.taskColumn = JSON.parse(localStorage.getItem(STORAGE_KEY) || '[]');
  },
  methods: {
    add: function(){
      if(this.newTask) {
        this.taskColumn[0].tasks.push({name: this.newTask, id: this.taskColumn[0].tasks.length});
        this.newTask = "";
        localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));
      }
    },
    createColumn: function(){
      if(this.newColumn) {
        this.taskColumn.push({title: this.newColumn, id: this.taskColumn.length, tasks: [] });
        this.newColumn = "";
        localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));
      }
    },

    remove: function(index, i) {
      this.taskColumn[i].tasks.splice(index,1);
     localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));

    },

    deleteColumn: function( i) {
      this.taskColumn.splice(i,1);
     localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));

    },
    editTask(task) {
      this.editedTask = task;
    },
    editColumn(column) {
      this.editedColumn = column;
    },

    doneEditTask(task, index, i) {
      if(!this.editedTask) {
        return
      }
      this.editedTask = null;
      task.name = task.name.trim();
      if(!task.name){
        this.remove(index, i);
      }
      localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));
    },

     doneEditColumn(column, index) {
      if(!this.editedColumn) {
        return
      }
      this.editedColumn = null;
      column.title = column.title.trim();
      if(!column.title){
        this.deleteColumn(index);
      }
      localStorage.setItem(STORAGE_KEY, JSON.stringify(this.taskColumn));
    }
    
  }
};
</script>

<style>
@import './assets/index.scss';
</style>
